.nav {
  display: flex;
  background-color: black;
  color: white;
  padding-left: 20px;
}

.button-list {
  text-align: center;
}

.item {
  border-bottom: 1px solid black;
}

.item h2 {
  padding-left: 20px;
  display: inline-block;
}

.item p {
  padding-left: 20px;
}

.modal {
  background-color: bisque;
  border: 1px solid black;
  margin: 30px;
  height: 300px;
  text-align: center;
}

.push-title {
  text-align: center;
  margin-top: 10px;
  border-bottom: 2px solid black;
}

.push-title input {
  margin-top: 20px;
  font-size: 20px;
}

.push-title p {
  font-size: 14px;
}

.push-title button {
  margin-bottom: 20px;
}